.userprofile_changeActionDiv__1yRRW{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: auto;
    border-bottom: 0.5px solid #c1c1c1;

}
.userprofile_emailActionBtn__3FSj8{
    width: auto;
    height: 2rem;
    font-family: 'Futura PT', sans-serif;
    font-size: 2rem ;
    font-weight: 500 ;
    color: #000000 ;
    letter-spacing: 1px;
    cursor: pointer;
    text-decoration: none;
}
.userprofile_emailActionBtn__3FSj8::after{
    content: '';
    display: block;
    width: 0;
    height: 0.2rem;
    background: #000000;
    transition: width .3s;
    margin-top: 2px;  

}
.userprofile_emailActionBtn__3FSj8:hover::after{
    width: 100%;
    transition: width .3s;
}
